import { Box, Button } from "@material-ui/core";
import React, { useCallback, useEffect, useRef, useState } from "react";
import styled from "styled-components";
import { useTranslation } from "react-i18next";
import 'react-awesome-slider/dist/styles.css';
import { useNavigate } from "react-router-dom";
import useMatchBreakpoints from "../../../hooks/useMatchBreakpoints";
import styles from './css/MobileAbouts.module.css';


const PanelPage = styled(Box)<any>`
    background-image: url(/image/pc/back.png);
`;



const MobileAbouts: React.FC = () => {
  const { isDesktop, isMobile } = useMatchBreakpoints();
  const { t } = useTranslation();
  const navigate = useNavigate();
  const data = {
    list_NNmg7H93: [null, null, null],
  };

  return (
    <PanelPage className={`flex-col ${styles['page']}`}>
       <div className={`flex-col ${styles['page']}`}>

      <div className={`flex-col flex-auto ${styles['group']} ${styles['space-y-49']}`}>
        <div className={`flex-col ${styles['group_2']}`}>
          <div className={`flex-col ${styles['section']} ${styles['space-y-27']}`}>
            <div className={`flex-col justify-start items-center self-center ${styles['text-wrapper']}`}>
              <h1 className={`${styles['font_2']} ${styles['text_2']}`}>About ElfinGuard</h1>
            </div>
            <span className={`${styles['font_3']} ${styles['text_3']}`}>
            Unleash the Power of Web 3.0 with Protocol Guardian: Your Access Control Shield.
            </span>
          </div>
          <div className={`flex-col items-center ${styles['group_3']} ${styles['space-y-9']}`}>
            <span className={`${styles['text_4']}`}>Unlocking the Power of Decentralized Storage: ElfinGuard's Revolutionary Access Control and PubSub Solution</span>
            <div className={`${styles['section_2']}`}></div>
          </div>
          <div className={`flex-col ${styles['group_4']} ${styles['space-y-49']}`}>
            <span className={`${styles['font_3']} ${styles['text_5']}`}>
              Experience the next generation of secure file accessibility with ElfinGuard in Web 3.0. 
              Our innovative protocol harnesses the power of smart contracts to manage file accessibility, providing unparalleled data security for content creators and their audiences. The ElfinGuard Access Control and PubSub protocol, a game-changer in decentralized technology. Say goodbye to the high costs of blockchain and embrace a scalable, low-cost solution.
            </span>
            <div className={`flex-col ${styles['space-y-38']}`}>
                <div  data-wow-duration="1s" data-wow-delay="0.2s"    className={`wow fadeInUp flex-row  ${styles['list-item']} ${styles['space-x-53']}`} >
                  <img
                    className={`shrink-0 ${styles['image_3']}`}
                    src="/image/750/about/编组 3.png"
                  />
                  <div className={`flex-col flex-auto ${styles['group_5']} ${styles['space-y-17']}`}>
                    <span className={`self-start ${styles['font_4']}`}>Trustworthy Authorization Process</span>
                    <span className={`${styles['font_5']} ${styles['text_6']}`}>
                      Utilizes blockchain's RPC nodes for transparent and secure authorization
                    </span>
                  </div>
                </div>

                <div   data-wow-duration="1s" data-wow-delay="0.4s"  className={`wow fadeInUp flex-row  ${styles['list-item']} ${styles['space-x-53']}`} >
                  <img
                    className={`shrink-0 ${styles['image_3']}`}
                    src="https://codefun-proj-user-res-1256085488.cos.ap-guangzhou.myqcloud.com/63a7fdfb5a7e3f0310febbfd/63ad399a717ee30011337499/16889566442086386631.png"
                  />
                  <div className={`flex-col flex-auto ${styles['group_5']} ${styles['space-y-17']}`}>
                    <span className={`self-start ${styles['font_4']}`}>Seamless User Experience</span>
                    <span className={`${styles['font_5']} ${styles['text_6']}`}>
                       Integrates smart contracts, re-cryptors, and decentralized storage into a user-friendly interface
                    </span>
                  </div>
                </div>

                <div  data-wow-duration="1s" data-wow-delay="0.6s" className={`wow fadeInUp flex-row ${styles['list-item']} ${styles['space-x-53']}`} >
                  <img
                    className={`shrink-0 ${styles['image_3']}`}
                    src="https://codefun-proj-user-res-1256085488.cos.ap-guangzhou.myqcloud.com/63a7fdfb5a7e3f0310febbfd/63ad399a717ee30011337499/16889566442086386631.png"
                  />
                  <div className={`flex-col flex-auto ${styles['group_5']} ${styles['space-y-17']}`}>
                    <span className={`self-start ${styles['font_4']}`}>Audience-Driven File Decryption</span>
                    <span className={`${styles['font_5']} ${styles['text_6']}`}>
                    Empowers content owners with control and ensures only qualified individuals can decrypt and access the content
                    </span>
                  </div>
                </div>
            </div>
          </div>
          <div className={`flex-col ${styles['group_6']} ${styles['space-y-47']}`}>
            <img
              className={`self-center ${styles['image_4']}`}
              src="/image/750/about/编组 8.png"            />
            <div  data-wow-duration="1s" data-wow-delay="0.2s" className={`wow fadeInUp flex-col ${styles['group_7']}`}>
              <span className={`self-start ${styles['font_2']}`}>Why We Started？</span>
              <span className={` ${styles['font_3']} ${styles['text_8']}`}>
                   At ElfinGuard, our story began between two teams (Paralinker and smartBCH) with a shared vision: to transform the way data is protected and accessed in Web 3.0. We witnessed the vulnerabilities of traditional systems and were inspired to harness the power of decentralization and blockchain technology.
              </span>
              <span className={`${styles['font_3']} ${styles['text_8']}`}>
                   Driven by a passion for data security, we developed ElfinGuard. Our revolutionary protocol empowers content creators by giving them precise control over who can access their files and under what conditions.
                <br />
              </span>
              <span className={`${styles['font_3']} ${styles['text_9']}`}>
                  Together, let's redefine access control and empower individuals with the power of data security and privacy.
              </span>
            </div>
          </div>
          <div className={`flex-col items-center ${styles['group_8']} ${styles['space-y-45']}`}>
            <img
              className={`${styles['image_5']}`}
              src="/image/750/about/编组 2 (1).png"               />
            <div  data-wow-duration="1s" data-wow-delay="0.2s" className={`wow fadeInUp flex-col ${styles['space-y-28']}`}>
              <span className={`self-start ${styles['font_2']}`}>Who runs ElfinGuard?</span>
              <span className={`${styles['font_3']} ${styles['text_10']}`}>
               The Paralinker team with extensive experience in cloud service infrastructure and The SmartBCH team with its expertise in SGX-based Enclave applications,
               both firmly believe that the programmable content publishing system is a vital extension and complement to "programmable assets." This collaborative effort has led to the development of the ElfinGuard protocol cluster, showcasing an innovative and promising initial implementation that unlocks new possibilities and fosters development in the blockchain industry.
              </span>
            </div>
          </div>
        </div>
   
      </div>
    </div>
    </PanelPage>
  );
};

export default MobileAbouts;



